import {
	request
} from "../../request.js"
export default {
	data() {
		return {
			account_number_list: [],
			bank_id: 0,
			bankId: "",
			price: 0,
			userBalance:0,
			tixian_l:false,
			hasBankAccount:false,//是否存在银行卡
			tixian_type:0,//判断提现类型 0银行卡,1微信
			$src: this.$src
		};
	},
	onShow() {
		this.getAccountNumberList();
		this.getUserBalance();
	},
	methods: {
		// 银行提现
		bank() {
			this.tixian_type=0;
		},
		
		// 微信提现
		wx() {
			this.tixian_type=1;
		},
		//跳转添加银行卡页面
		goBankCard(){
			uni.navigateTo({
				url:"../bankcard/bankcard",
			})
		},
		getAccountNumberList() {
			request({
				url: "index.php?s=/wap/member/getBankList"
			}).then(res => {
				if(res.data.length >0){
					this.account_number_list = res.data;
					this.bankId = this.account_number_list[this.bank_id].id;
					this.hasBankAccount = true;
				}
				
			})
		},
		chooseBank(event) {
			console.log(event.detail.value);
			this.bank_id = event.detail.value;
			this.bankId = this.account_number_list[this.bank_id].id;
		},
		tixian() {
			if(this.tixian_l){
				return;
			}
			this.tixian_l = true;
			if (this.price == "") {
				uni.showToast({
					title: "提现金额不能为0",
					icon: "none"
				})
				this.tixian_l=false;
				return
			}
			
			if(parseFloat(this.price) > this.userBalance){ //要转成float在进行比较
				uni.showToast({
					title: "提现金额不能大于最大提现金额!",
					icon: "none"
				})
				this.tixian_l=false;
				return
			}
			let check_type = this.checkTixianType();
			if (!check_type) {
				this.tixian_l=false;
				return;
			} 
			this.tixian2();
		},
		tixian2(){
			request({
				url: "index.php?s=/wap/member/tixian",
				method: "POST",
				data: {
					cash: this.price,
					bank_acc_id: this.bankId,
				}
			}).then(res => {
				if(res.data.code>0){
					uni.showToast({
						title:res.data.msg,
						icon:"none"
					});
					setTimeout(function(){
						uni.navigateTo({
							url:"../money/money"
						})
					},1000)
					this.tixian_l = true;
				}else{
					uni.showToast({
						title:res.data.msg,
						icon:"none"
					})
					this.tixian_l = true;
				}
			})
		},
		//获取会员余额
		getUserBalance(){
			request({
				url:"index.php?s=/wap/member/getMomeyInfo",
			}).then(res=>{
				this.userBalance=res.data.data.balance;
			})
		},
		//提现类型逻辑判断
		checkTixianType(){
			if(this.tixian_type ==0){
				if(!this.hasBankAccount){
					uni.showModal({
						title:"提示",
						content:"您还没绑定银行卡,点击确定跳转到绑定页面!",
						cancelText:"暂不",
						success: (res) => {
							if(res.confirm){
								uni.navigateTo({
									url:"../bankcard/bankcard",
								})
							}else{
								return false;
							}
						}
					})
					return false;
				}
			}else if(this.tixian_type ==1){
				//微信提现的逻辑 目前没有等有了后在这里添加
				uni.showToast({
					title:"暂不支持微信提现!",
					icon:"none"
				})
				return false;
			}
			return true;
		}
	}
}
